import { Component, h, Prop, Element, Host } from '@stencil/core';

@Component({
  tag: 'ks-order-toggle',
})
export class OrderChoice {
    @Element() root: HTMLElement;

    @Prop({reflect: true}) name: string;
    @Prop() value: string;
    @Prop() text: string;
    @Prop({mutable: true, reflect: true}) active: boolean;
    @Prop() changeHandler: (value: string)=>void;

    render() {
        return <Host>
            <input hidden type="radio" name={this.name} value={this.value} checked={this.active} />
            <ks-button name={this.text} onClick={()=>this.changeHandler(this.value)} pressed={this.active} border={!this.active}/>
        </Host>
    }
}